import React from "react";
import { HashRouter, Switch } from "react-router-dom";
import { renderRoutes } from "react-router-config";
import routes from "@router";

const Main = (props) => {
  return (
    <HashRouter>
      <Switch>
        <>{renderRoutes(routes)}</>
      </Switch>
    </HashRouter>
  );
};

export default Main;
